
.music-player {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  overflow: hidden;
}
.music-player .music-box {
  width: 100%;
  white-space: nowrap;
}
/*  */
.music-title i:nth-child(1) {
  padding-left: 3rem;
  margin-right: 11.55555556rem;
}
.music-title .fa-chevron-up {
  padding-left: 2rem;
}
.music-left b {
  font-size: 4rem;
}
/* cd */
#app .music-player .music-box .play-box {
  width: 18rem;
  height: 18rem;
  top: 12rem;
  left: 50%;
  margin-left: -9rem;
}
#app .music-box .play-box img {
  border-radius: 50%;
}
.music-player .music-box .play-box img {
  width: 100%;
  height: 100%;
}
.fade-enter-active {
  -webkit-animation: fade-in 0.8s;
          animation: fade-in 0.8s;
}
.fade-leave-active {
  -webkit-animation: fade-out 0.8s;
          animation: fade-out 0.8s;
}
@-webkit-keyframes fade-in {
0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -74rem, 0);
            transform: translate3d(0, -74rem, 0);
}
50% {
    opacity: 0.5;
    -webkit-transform: translate3d(0, -37rem, 0);
            transform: translate3d(0, -37rem, 0);
}
100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0px, 0);
            transform: translate3d(0, 0px, 0);
}
}
@keyframes fade-in {
0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -74rem, 0);
            transform: translate3d(0, -74rem, 0);
}
50% {
    opacity: 0.5;
    -webkit-transform: translate3d(0, -37rem, 0);
            transform: translate3d(0, -37rem, 0);
}
100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0px, 0);
            transform: translate3d(0, 0px, 0);
}
}
@-webkit-keyframes fade-out {
0% {
    opacity: 1;
}
50% {
    opacity: 0.5;
    -webkit-transform: translate3d(0, -37rem, 0);
            transform: translate3d(0, -37rem, 0);
}
100% {
    opacity: 0;
    -webkit-transform: translate3d(0, -74rem, 0);
            transform: translate3d(0, -74rem, 0);
}
}
@keyframes fade-out {
0% {
    opacity: 1;
}
50% {
    opacity: 0.5;
    -webkit-transform: translate3d(0, -37rem, 0);
            transform: translate3d(0, -37rem, 0);
}
100% {
    opacity: 0;
    -webkit-transform: translate3d(0, -74rem, 0);
            transform: translate3d(0, -74rem, 0);
}
}
.music-player .music-left,
.music-player .music-right {
  width: 100%;
  display: inline-block;
  vertical-align: top;
  height: 40rem;
  color: #fff;
  /* margin: 0.95rem; */
  font-size: 1.7rem;
  overflow: hidden;
  text-align: center;
}
.music-player .music-box .lyric .lyric-box {
  height: 100%;
}
.music-player .currentlyc {
  color: seagreen;
  /* animation: lyric 0.5s; */
  -webkit-transform: scale(1.5, 1.5);
          transform: scale(1.5, 1.5);
}
@-webkit-keyframes lyric {
0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
}
100% {
    opacity: 0;
    -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
}
}
@keyframes lyric {
0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
}
100% {
    opacity: 0;
    -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
}
}
.sentence-lyric-wrapper {
  width: 100%;
  margin: 1.2rem auto 0 auto;
  position: absolute;
  top: 34rem;
  text-align: center;
}
.sentence-lyric {
  height: 2.4rem;
  line-height: 2.4rem;
  font-size: 1.8rem;
  color: rgba(255, 255, 255, 0.8);
}

/*  */
.music-player .music-box .qiehuan .active {
  background-color: salmon;
}
.music-player .schedule .progress-bar {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  width: 34rem;
  height: 0.6rem;
  border-radius: 0.5rem;
  /* border-bottom: 0.5rem solid #666666; */
  background-color: #666666;
  /* background: hsla(0,0%,96%,.3); */
  margin: 1.85185185rem auto 0;
  padding-top: 0;
  font-size: 0;
  cursor: pointer;
  position: relative;
}
.music-player .schedule .currentProgress {
  display: inline-block;
  width: 0;
  height: 0.6rem;
  background: red;
  float: left;
  background: #c62f2f;
}
.music-player .schedule .ball {
  width: 1.6rem;
  height: 1.6rem;
  margin-top: -0.35rem;
  position: absolute;
  background: #fff;
  border-radius: 50%;
  float: left;
  cursor: pointer;
}
.music-player .schedule span {
  font-size: 1.4rem;
  line-height: 4.4rem;
}



