
#app .photo-box i {
  margin: 0;
  padding: 1.85rem;
}
.transmit p {
  line-height: 2.59rem;
}
.nomessage {
  width: 100%;
  height: 20rem;
  position: relative;
  background: #ffffff;
}
.nomessage img {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -7.4rem;
  margin-top: -7.59rem;
}
.weui-dialog {
  max-width: 55.56rem !important;
  width: 80% !important;
  height: 18.52rem;
}
.weui-dialog__hd,
.weui-dialog__title {
  padding: 0 0.5em 0.5em !important;
  text-align: left !important;
  height: 5.56rem;
  line-height: 6.67rem;
  font-size: 2.22rem !important;
}
.weui-dialog__bd,
.weui-dialog__bd p {
  padding: 0.2em 0.5em 0.5em !important;
  text-align: left !important;
  height: 8.15rem;
  line-height: 2.96rem;
  font-size: 1.85rem !important;
}
.weui-dialog__ft {
  height: 4.45rem;
  font-size: 1.85rem !important;
}
.weui-dialog__ft .weui-dialog__btn {
  float: right;
}
#app .dynamic .weui-actionsheet {
  width: 50% !important;
}
#app .dynamic .weui-actionsheet .weui-actionsheet__cell {
  height: 4.44rem;
  line-height: 3.35rem;
  font-size: 1.44rem;
  letter-spacing: 0.15rem;
  background: none;
  float: none;
}
#app .dynamic .weui-actionsheet__menu,
#app .dynamic .weui-actionsheet {
  width: 20rem;
}
#app .dynamic .weui-actionsheet__menu div {
  width: 100%;
  color: #000;
}
.photo-box {
  background: #181818;
  background-size: 100% 81.78rem;
  width: 100%;
  height: 81.78rem;
  overflow: auto;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 99;
}
.photo-box i {
  font-size: 1.85rem;
  color: #ffffff;
  display: block;
  margin: 1.85rem;
}
.photo-box img {
  width: 21.48rem;
  height: 21.48rem;
  border-radius: 0;
  margin: 0.56rem;
}



