
#app .slideshow .circle{
	left:50%;
	padding:0;
}
#app .slide_wrap .list{
	width:800%;
}
.slideshow .circle .white:last-child{
	margin-right:0;
}
.slide_wrap{
	height: 100%;
}
.slide_wrap .list {
	list-style: none;
	margin: 0px;
	padding: 0;
	width: 600%;
	height: 100%;
	-overflow: hidden;
	position: absolute;
	left: 0;
	top:0;
}
.slide_wrap .list li{
	height:100%;
	float: left;
	width:46rem;
}
.slide_wrap .list img {
	width:46rem;
}
.slide_wrap .tagClient {
	width: 100%;
	height:100%;
	position: relative;
	overflow: hidden;
}
.slide_wrap .circle div.active{
	background-color: skyblue;
}



