
#app .vux-search-fixed{
	top:0!important;
}
#app button.weui-btn{
	height:3.4rem;
	font-size: 1rem;
}
#app .weui-btn_primary{
	background: #444;
	color:#fff;
}
#app .search-seek{
    position: absolute;
    top:0;
    left:0;
}
.subject-term .theme{
    width:70rem;
}
.special .theme{
    width:114rem;
}
.seek input{
    width: 44rem;
    height: 3.40740741rem;
    background: #ffffff;
    border-radius: 0.37037037rem;
    text-indent: 16.07407407rem;
    font-size: 1.48148148rem;
    color: #7f7f7f;
}
#app .weui-search-bar{
  width:100%;
  height: 4.81481481rem;
  padding: 0.74074074rem 1rem;
  background: #232427;
  position: relative;
	z-index: 99;
}
#app .weui-search-bar__form{
   border-radius: 0.37037037rem;
}
#app .weui-icon-search{
    font-size: 1.85rem;
    line-height: 3.41481481rem;
}
#app .weui-search-bar__box{
    /* padding-left: 80px; */
}
#app .weui-search-bar__input{
    width: 36rem;
    height: 2.81481481rem;
    line-height: 3.81481481rem;
    background: #ffffff;
    -text-indent: 16.07407407rem;
    font-size: 1.48148148rem;
    color: #7f7f7f;
		text-indent: 2.5rem;
}
#app .weui-icon-clear{
    font-size: 1.85rem;
    line-height: 3.41481481rem;
}
#app .weui-search-bar__cancel-btn{
    font-size: 1.25rem;
    line-height: 3.41481481rem;
    color: #ffffff;
}
#app .weui-cell{
    height: 80px;
    font-size: 30px;
}
.seek{
    position: absolute;
    top:0;
    left:0;
}
.vux-search_show {
    display: none;
}
.subject-term .theme{
    width:70rem;
}
.special .theme{
    width:114rem;
}



